html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  font-family: 'Barlow Condensed', sans-serif;
  background-size: cover;
}

a {
  text-decoration: none;
}

.layout {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0px;
  background-color: #ededed;
  line-height: 18px;
  margin-top: 30px;
}

.container {
  display: flex;
  flex-direction: row;
  margin: 30px;
}

.blog {
  margin-right: 32px;
}

.header {
  padding: 25px 0 25px 32px;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: row;
  background-position: center center;
  background-size: cover;
}

.header__logo {
  margin-right: 45px;
}

.header__site-logo {
  width: 140px;
  height: 58px;
  background-repeat: no-repeat;
  border-bottom: 1px solid #6892a0;
  display: inline-block;
}

.header__site-title {
  font-family: 'Lucida Sans Unicode', arial;
  text-transform: uppercase;
  font-size: 10px;
  color: #446c7a;
  padding: 10px 0 0 0;
  text-align: center;
  width: 140px;
}

.header__topnav a {
  text-decoration: none;
  color: #a6a6a6;
  margin-right: 10px;
  text-transform: uppercase;
  font-size: 10px;
}

.header__botnav {
  padding-top: 22px;
  margin-left: -16px;
}

.header__botnav a {
  text-decoration: none;
  color: #fff;
  text-transform: uppercase;
  font-size: 14px;
  padding: 10px 16px;
  display: inline-block;
}

.header__botnav:hover {
  background-color: transparent;
}

.topbar {
  display: flex;
  background: #fff;
  background: linear-gradient(#ffffff 0%, #d9dbdc 100%);
  padding: 10px 0px 10px 0px;
  width: 100%;
  font-size: 14px;
  flex-wrap: wrap;
}

.topbar a {
  text-decoration: none;
  color: #757575;
}

.topbar__wraper {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.topbar__tag {
  display: inline-block;
  text-transform: uppercase;
  background-color: #85afbd;
  border-radius: 2px;
  color: #fff;
  margin: 0 0 0 32px;
  padding: 1px 3px;
}

.topbar__link {
  margin-left: 12px;
  font-style: italic;
  font-family: 'Georgia', sans-serif;
  color: #757575;
  flex-grow: 1;
}

.topbar__arrow {
  margin-left: 30px;
}

.topbar__search {
  display: flex;
  margin-left: 88px;
}

.topbar__search input[type=text] {
  height: 26px;
  padding: 0 30px;
  border-radius: 26px;
  font-style: italic;
  outline: none;
  color: #757575;
}

.topbar__search-img {
  width: 18px;
  height: 18px;
  background-position: center center;
  background-size: cover;
  margin-top: 6px;
  margin-left: -30px;
}

.main-title {
  padding: 8px;
  background-color: #ae0002;
  font-size: 18px;
  color: #fff;
  text-transform: uppercase;
  border-radius: 3px;
  overflow: hidden;
}

.post-title {
  display: flex;
  flex-direction: row;
  align-items: center;
  background-color: #cacaca;
  border-radius: 3px;
  overflow: hidden;
}

.post-title__l {
  padding-right: 35px;
  border-radius: 3px 0px 0px 3px;
}

.post-title__arrow {
  margin-left: -10px;
  padding: 17px;
  background-color: #ae0002;
  border-radius: 1px;
  transform: rotate(45deg);
}

.post-title__r {
  color: #757575;
  font-family: 'Georgia', sans-serif;
  font-size: 12px;
  padding-left: 18px;
  white-space: nowrap;
  overflow: hidden;
}
